@import "_mixins.scss";

.capstones{
  box-sizing: border-box;
  padding: 7.5% 0%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.capstones-title{
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  margin-bottom: 7.5%;
}
.capstones-line{
  position: absolute;
  left: 50%;
  width: 1px;
  top: 10%;
  height: 90%;
  background: rgba($text, 0.4);
}

.capstones-cards-container{
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
}

.capstone-card{
  width: 40%;
  height: 40vh;
  position: relative;
  box-sizing: border-box;
  margin-bottom: 5%;
  cursor: pointer;
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  .capstone-card-image{
    background-size: cover;
    background-position: center;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: all 0.25s;
    filter: grayscale(100%);
    opacity: 0.4;
    -webkit-box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
    -moz-box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
    box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
  }
  &:hover .capstone-card-image{
    filter: none;
    opacity: 0.8;
    transform: scale(1.1);
  }
  .capstone-card-content{
    position: absolute;
    display: flex;
    flex-direction: column;
    bottom: 25%;
  }
  h1, p{
    margin: 0%;
  }
  h1{
    font-size: 3.5rem;
    margin-bottom: 2.5%;
  }
  p{
    width: 60%;
    line-height: 1.35rem;
    font-size: 0.9rem;
    font-weight: 400;
    transition: opacity 0.25s;
  }
  &:nth-child(odd){
    .capstone-card-content{
      right: -10%;
      align-items: flex-end;
    }
    p{
      text-align: right;
    }
  }
  &:nth-child(even){
    margin-top: 7.5%;
    .capstone-card-content{
      left: -10%;
      align-items: flex-start;
    }
  }
  @include laptop{
    height: 50vh;
    h1{
      font-size: 2.75rem;
    }
    p{
      font-size: 0.8rem;
    }
  }
}
